import { Component, OnInit } from '@angular/core';
import { NgModule } from '@angular/core';
import { heroes } from './heroes';
import { Server } from '../server/server';
import { DraftResultComponent } from '../draft-result/draft-result.component';

class EmptyHero {
  name = "empty";
  isPickTime = false;
}
var fillHeroes = () => {
  var arr = [];
  for (var index = 0; index < 5; index++) {
    arr.push(new EmptyHero());
  }
  return arr;
}

@Component({
  selector: 'app-heroes-list',
  templateUrl: './heroes-list.component.html',
  styleUrls: ['./heroes-list.component.css']
})
export class HeroesListComponent implements OnInit {
  heroes = [];
  bansP1 = fillHeroes();
  bansP2 = fillHeroes();
  picksP1 = fillHeroes();
  picksP2 = fillHeroes();
  time = 30;
  bonusTimeP1 = 130;
  bonusTimeP2 = 130;
  curPlayer = 1;
  splashText = "";
  isLoading = false;
  //�-�-�-�-�-��-�-�-�-�-�-�-�-�-�-�-�-�-�
  order = ["b1", "b2", "b1", "b2", "p1", "p2", "p2", "p1", "b1", "b2", "b1", "b2", "p2", "p1", "p2", "p1", "b2", "b1", "p2", "p1"]
  currOrder = 0;
  turnTime;
  isComplete = false;
  page = 'pick';
  constructor(private appService: Server) {
    this.heroes = heroes;
    this.currOrder = 0;
    this.turnTime = setInterval(() => this.tick(), 1000);
    this.splashText = "BAN";;
    this.bansP1[0].isPickTime = true;
  }
  ngOnInit() {
  };
  getStrHeroes() {
    return this.heroes.filter(hero => hero.group == 0);
  }
  getAgiHeroes() {
    return this.heroes.filter(hero => hero.group == 1);
  }
  getIntHeroes() {
    return this.heroes.filter(hero => hero.group == 2);
  }
  heroClick(id) {
    if (heroes.find(hero => hero.id == id).banned == true) return;
    heroes.find(hero => hero.id == id).banned = true;
    if (this.order[this.currOrder] == "b1") this.bansP1[this.bansP1.findIndex(el => el.name == "empty")] = heroes.find(hero => hero.id == id);
    if (this.order[this.currOrder] == "b2") this.bansP2[this.bansP2.findIndex(el => el.name == "empty")] = heroes.find(hero => hero.id == id);
    if (this.order[this.currOrder] == "p1") this.picksP1[this.picksP1.findIndex(el => el.name == "empty")] = heroes.find(hero => hero.id == id);
    if (this.order[this.currOrder] == "p2") this.picksP2[this.picksP2.findIndex(el => el.name == "empty")] = heroes.find(hero => hero.id == id);
    this.currOrder++;

    if (this.currOrder >= this.order.length) { this.finish(); return; }

    if (this.order[this.currOrder] == "b1") this.bansP1[this.bansP1.findIndex(el => el.name == "empty")].isPickTime = true;
    if (this.order[this.currOrder] == "b2") this.bansP2[this.bansP2.findIndex(el => el.name == "empty")].isPickTime = true;
    if (this.order[this.currOrder] == "p1") { this.picksP1[this.picksP1.findIndex(el => el.name == "empty")].isPickTime = true; }
    if (this.order[this.currOrder] == "p2") this.picksP2[this.picksP2.findIndex(el => el.name == "empty")].isPickTime = true;
    if (this.curPlayer == 1) this.curPlayer = 2
    else this.curPlayer = 1;

    if (this.order[this.currOrder].includes("b")) this.splashText = "BAN";
    if (this.order[this.currOrder].includes("p")) this.splashText = "PICK";

    this.time = 30;
    clearInterval(this.turnTime);
    this.turnTime = setInterval(() => this.tick(), 1000);
  }
  tick() {
    if (this.time > 0)
      this.time -= 1;
    else if (this.curPlayer == 1) this.bonusTimeP1 -= 1;
    else this.bonusTimeP2 -= 1;
  }
  finish() {
    this.splashText = "";
    this.isComplete = true;
    clearInterval(this.turnTime);
  }
  getResult() {
    // if (this.isLoading) return;
    this.isLoading = false;
    this.page = 'result';
    // this.isLoading = true;
    // this.picksP1.forEach(el => {
    //   el = this.appService.getHeroMathsUp(el);
    // });
    // var hl = this;
    // var checkMatchups = setInterval(() => {
    //   // if (hl.picksP1.every(el => el.matchups != null)) {
    //   clearInterval(checkMatchups);
    //   hl.isLoading = false;
    //   hl.page = 'result';
    //   // }
    // }, 500);
  }
  secToTime(sec) {
    return ~~(sec / 60) + ":" + (sec % 60 < 10 ? "0" : "") + sec % 60;
  }
}



