import { Component, Input, OnInit } from '@angular/core';
import { heroes } from '../heroes-list/heroes';
import { adv } from '../heroes-list/advtable';
import { wins } from '../heroes-list/winstable';

@Component({
  selector: 'app-draft-result',
  templateUrl: './draft-result.component.html',
  styleUrls: ['./draft-result.component.css']
}) export class DraftResultComponent implements OnInit {
  @Input() picksP1: any;
  @Input() picksP2: any;
  p1WP : number;
  p2WP : number;
  winText = "Team 1 Win";
  constructor() {

  }
  ngOnInit() {
    adv.adv_rates.forEach((w, index) => {
      this.picksP1.forEach(p1 => {
        if (p1.id - 1 == index) {
          p1.winScore = [];
          this.picksP2.forEach(p2 => {
            p1.winScore.push(w[p2.id - 1][1]);
          });
          p1.totalWins = p1.winScore.reduce((a, b) => a + b, 0);
          p1.totalWins = Number(p1.totalWins.toFixed(2));
        }
      });
      this.picksP2.forEach(p1 => {
        if (p1.id - 1 == index) {
          p1.winScore = [];
          this.picksP1.forEach(p2 => {
            p1.winScore.push(w[p2.id - 1][1]);
          });
          p1.totalWins = p1.winScore.reduce((a, b) => a + b, 0);
           p1.totalWins = Number(p1.totalWins.toFixed(2));
        }
      });
    });
    adv.adv_rates_friends.forEach((w, index) => {
      this.picksP1.forEach(p1 => {
        if (p1.id - 1 == index) {
          p1.friendScore = [];
          this.picksP1.forEach(p2 => {
            if(p2!=p1)
               p1.friendScore.push(w[p2.id - 1][1])
               else  p1.friendScore.push(0)
          });
          p1.totalFriend = p1.friendScore.reduce((a, b) => a + b, 0);
          p1.totalFriend = Number(p1.totalFriend.toFixed(2));
        }
      });
      this.picksP2.forEach(p1 => {
        if (p1.id - 1 == index) {
          p1.friendScore = [];
          this.picksP2.forEach(p2 => {
             if(p2!=p1)
            p1.friendScore.push(w[p2.id - 1][1]);
            else  p1.friendScore.push(0)
          });
          p1.totalFriend = p1.friendScore.reduce((a, b) => a + b, 0);
          p1.totalFriend = Number(p1.totalFriend.toFixed(2));
        }
      });
    });

    this.p1WP = 50 + this.picksP1.map(obj =>  obj.totalWins).reduce((a, b) => a + b, 0);
    this.p2WP = 50 + this.picksP2.map(obj =>  obj.totalWins).reduce((a, b) => a + b, 0);
    this.p1WP += this.picksP1.map(obj =>  obj.totalFriend).reduce((a, b) => a + b, 0)/2;
    this.p2WP += this.picksP2.map(obj =>  obj.totalFriend).reduce((a, b) => a + b, 0)/2;
    if (this.p1WP > this.p2WP) this.winText = "Team 1 Win";
    else this.winText = "Team 2 Win";
      this.p1WP =  Number(this.p1WP.toFixed(2));
      this.p2WP = Number(this.p2WP.toFixed(2));  
    };
}


